.parenttop {
display: grid;
grid-template-columns: 120px 1fr;
grid-template-rows: 120px;
grid-column-gap: 0px;
grid-row-gap: 0px;
}

.div1top { grid-area: 1 / 1 / 2 / 2; }
.div2top { grid-area: 1 / 2 / 2 / 3; }
.div3top { grid-area: 2 / 1 / 3 / 3; margin-top: 36px;}

.buttons{
  background-color: #ad8ef5;
  border-radius: 50px;
  margin-right: 8px;
	background: -webkit-linear-gradient(#ad8ef5 5%, #8F7EB8 100%);
  background: -moz-linear-gradient(#ad8ef5 5%, #8F7EB8 100%);
  background: -o-linear-gradient(#ad8ef5 5%, #8F7EB8 100%);
  background: linear-gradient(#ad8ef5 5%, #8F7EB8 100%);
}

.firstplace {
  z-index: 10
}

.firstplace img{
  position: absolute;
  width: 38px;
  height: 36px;
  margin-left: -40px;
  margin-top: 40px;
  z-index: 10
}
