@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100&display=swap');

.cent {
	  display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 32;
}

.counter{
	background-color: #ad8ef5;
	position: absolute;
	margin-left: 30px;
	width: 24px;
	height: 24px;
	text-align: center;
	border-radius: 15px;
	margin-top: 40px;
	border: 4px solid white;
}

.counter a{
	display: block;
	color: white;
}

.header{
	margin-top: -8px;
	height: 200px;
	background-color: #ad8ef5;
	background-image: url(https://i.imgur.com/loicuvI.png);
	background-size: 750px;
	background-position: center;
	background-repeat: repeat-y;
	animation: animatedBackground 30s linear infinite;
}

@keyframes animatedBackground {
  from { background-position: 0 0; }
  to { background-position: 0% 100%; }
}

.header h1{
	font-family: 'Roboto Mono', monospace;
	font-size: 80px;
	display:inline-block;
	color: white;
	margin: auto;
	margin-top: 8px;
}

.underheader{
	height: 50px;
	background-color: white;
	margin-top: -50px;
	border-top-left-radius: 50px;
	border-top-right-radius: 50px;
}

.score{
	background-color: white;
	width: 40%;
	margin-top: -60px;
	border-radius: 25px;
	-webkit-box-shadow: 0px 4px 54px 5px rgba(77, 86, 92, 0.42);
  -moz-box-shadow: 0px 4px 54px 5px rgba(77, 86, 92, 0.42);
  box-shadow: 0px 4px 54px 5px rgba(77, 86, 92, 0.6);
}

@import url('https://fonts.googleapis.com/css?family=Overpass');
.score h1{
    font-family: Overpass, sans-serif;
    font-size: 35px;
		color: #ad8ef5;
		text-shadow: rgb(109, 120, 133) 1px 1px 1px;
}

.blocks{
  border-radius: 50px;
	margin: 8px;
	margin-left: 16px;
	margin-right: 16px;
}

.buttons{
  background-color: #ad8ef5;
  border-radius: 50px;
  margin-right: 8px;
	background: -webkit-linear-gradient(#ad8ef5 5%, #8F7EB8 100%);
  background: -moz-linear-gradient(#ad8ef5 5%, #8F7EB8 100%);
  background: -o-linear-gradient(#ad8ef5 5%, #8F7EB8 100%);
  background: linear-gradient(#ad8ef5 5%, #8F7EB8 100%);
}

.subbutton{
	color: #ad8ef5;
	border-radius: 25px;
}


.secondbuttons{
  background-color: #ad8ef5;
  border-radius: 30px;
	margin-top: 8px;
}
