.podimg{
	border-radius: 7% 93% 70% 30% / 17% 47% 53% 83%;
	width: 120px;
	height: 120px;
	margin: 18px;
	background-image: url(https://i.imgur.com/loicuvI.png);
	background-size: 800px;
	background-repeat: no-repeat;
	background-position: center;
}

.podimg img{
	border-radius: 50%;
	position: absolute;
	margin-left: 13px;
