	width: 70px;
	height: 70px;
	border: 8px solid white;
}

.parent {
display: grid;
grid-template-columns: 140px 1fr;
grid-template-rows: 140px;
grid-column-gap: 0px;
grid-row-gap: 0px;
margin-top: -25px;
}

.parenttext{
  margin-top: 16px;
}

.parentinfo{
	margin-left: -18px;
}

.div1 { grid-area: 1 / 1 / 2 / 2; }
.div2 { grid-area: 1 / 2 / 2 / 3; }

.chatbox{
	position: fixed;
	background-color: #ad8ef5;
	width: 300px;
	height: 200px;
	bottom: 0;
	margin-bottom: 70px;
	z-index: 100;
}

